<template>
    <div class="info">
        <h3 class="title">{{pool == '' ? '' : pool.ptitle}}</h3>
        <div class="progress">
            <yd-progressbar :progress="pool == '' ? 0 : pool.cbill / pool.tbill" trail-width="4" stroke-color="#edf0f5" trail-color="#ffc054">
                    <van-col span="24" class="timeouts" v-show="isshow">
                        <van-icon name="clock-o" class="icon_clock"></van-icon>
                        <span>{{timeout == '' ? '' : timeout}}</span>
                    </van-col>
                    <van-col class="current" span="24">
                        <span class="money">{{ pool != '' ? pool.realBill + pool.realUnit : ''}}</span>
                    </van-col>
                    <van-col class="text" span="24">
                        <span>{{pool == '' ? '' : pool.billHint}}</span>
                    </van-col>
            </yd-progressbar>
        </div>
        <div class="payinfo">
            <van-row style="margin-bottom:0.56rem">
                <van-col span="12">
                    <span class="left">目标金额</span>
                </van-col>
                <van-col span="12" class="right">
                    <span class="right tbill">{{pool == '' ? '' : pool.rtbillValue + pool.rtbillUnit}}</span>
                </van-col>
            </van-row>
            <van-row style="margin-bottom:0.56rem">
                <van-col span="12" style="text-align:left">
                    <span class="left">份数</span>
                </van-col>
                <van-col span="12" class="right">
                    <p>
                        <span class="add btn">＋</span>
                        <span style="margin:0 0.3rem;">1</span>
                        <span class="incre btn">－</span>
                    </p>
                </van-col>
            </van-row>
            <van-row style="margin-bottom:0.56rem">
                <van-col span="12">
                    <span class="left">选择梦想</span>
                </van-col>
                <van-col span="12" class="right">
                    <span class="right">
                        <span id="dream"></span>
                        <van-icon name="arrow"></van-icon>
                    </span>
                </van-col>
            </van-row>
        </div>
    </div>
</template>

<script>
export default {
    name:'info',
    data () {
        return {
            isshow:true,//是否显示倒计时
            timeout:this.timeout(),//倒计时
        }
    },
    created(){
        this.selectDream(this,this.$route.params.type)
        if(this.$route.params.type == 'dream'){
            this.isshow = true
        }else if(this.$route.params.type == 'trade'){
            this.isshow = false
            $('.info .progress .current').css('margin-top',0)
        }
    },
    methods:{
        // 倒计时
        timeout(){
            var self = this;
            setInterval(function(){
                var dt = parseInt(new Date().getTime() / 1000)
                var timestamp = (parseInt(self.pool.ptime) + parseInt(self.pool.duration)) - dt;
                var h = Math.floor(timestamp / 3600) >= 10 ? Math.floor(timestamp / 3600) : '0' + Math.floor(timestamp / 3600);
                var m = Math.floor(timestamp / 60 % 60) >= 10 ? Math.floor(timestamp / 60 % 60) : '0' + Math.floor(timestamp / 60 % 60);
                var s = Math.floor(timestamp % 60) >= 10 ? Math.floor(timestamp % 60) : '0' + Math.floor(timestamp % 60);
                self.timeout = h + ':' + m + ':' + s;
            },1000)
        },
        // 查找梦想
        selectDream(app,type){
            /**
             * @params app Vue实例对象
             * @parmas type 支付类型
             */
            TD_Request('dr','gdream',{uid:app.$store.state.uid,did:app.$store.state.actions.pay.did},function(code,data){
                console.log(data)
            },function(code,data){
                console.log(data)
            })
        }
    },
    props:{
        pool:{
            type:[Array,Object],
            default:function(){
                return ''
            }
        }
    },
    watch:{
        pool(data){
            console.log(data)
        }
    }
}
</script>

